.header {
  padding: 15px 10px;
  border-bottom: 3px solid #fff;
  background: #02a1e4;
}

.header-content {
  max-width: 1370px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.header p {
  color: #fff;
  padding-top: 20px;
  padding-bottom: 5px;
  font-size: 17px;
}

.header a {
  color: #fff;
  padding-bottom: 10px;
  font-size: 17px;
}

.header h1 {
  font-size: 17px;
  padding-left: 15px;
  font-weight: 700;
}

.header-p {
  font-weight: 400;
}

.header-p-tel {
  padding: 0px;
  margin: 0px;
}

.col {
  padding-top: 110px;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.row-header {
  display: flex;
}

.col-right {
  padding: 2rem;
}

.slider-wrapper {
  position: relative;
  max-width: 48rem;
  margin: 0 auto;
}

.slider {
  display: flex;
  aspect-ratio: 7/3;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  box-shadow: 0 1.5rem 3rem -0.75rem hsla(0, 0%, 0%, 0.25);
  overflow: hidden;
}

.slider img {
  flex: 1 0 100%;
  scroll-snap-align: start;
  object-fit: cover;
}

.slider-nav {
  display: flex;
  column-gap: 1rem;
  position: absolute;
  bottom: 1.25rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.slider-nav a {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  background-color: #fff;
  opacity: 0.75;
  transition: opacity ease 250ms;
}

.slider-nav a:hover {
  opacity: 1;
}


.col-img {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 650px;
  height: 100px;
}

.header-name-company {
  color: #fff;
  font-weight: 300;
  font-size: 40px;
}


.header-u {
  color: #fff;
}